import React, { Component } from 'react';
import { Typography, Grid, Select, MenuItem, InputLabel, TextField, Divider } from '@material-ui/core';
import './Form.css';
import styled from 'styled-components';
import axios from 'axios';
import eventConfig from './eventConfig.json';

const HolaText = styled.div`
  text-align: center;
  font-size: 3vh;
  margin: 5vh 0vh 3vh;
`;

const UserValue = styled.div`
  font-size: 2vh;
`;

class EventSuccess extends Component{
  constructor(props){
    super(props);
    this.state={
      values:{
        module: '',
        moduleIndex: -1,
        event: ''
      }
    };
  }

  componentDidMount(){
    axios({
      method: 'GET',
      url: '/workshop/fetchEvent/'+this.props.match.params.id
    }).then((response)=>{
      this.setState({
        values: {
          ...response.data
        }
      });
    }).catch((err)=>{
      console.log(err);
    })
  }

  render(){
    const {values} = this.state;
    let selectedEvent=[];
    if(values.module!==''){
      let finalEvent=eventConfig.filter((event)=>{
        return event.name===values.module;
      });
      selectedEvent=[
        ...finalEvent[0].event
      ];
    }
    return(
      <Grid container spacing={0}>
        <Grid style={{ textAlign: 'center' }} item sm={12}>
          <Typography component="h3"  variant="h3" style={{ fontFamily: 'Guardian', color: '#00d2ff' }}>
              Tecnoesis 2020
          </Typography>
          <Grid container spacing={0} style={{ margin: '20px 0' }}>
            <Grid item sm={12}>
            <Grid item xs={12} sm={12} style={{margin: '5% 0%', textAlign: 'center', fontSize: '2.5vh'}}>
          Successful Registration For Event
        </Grid>
        <Divider variant="middle" />
        <Grid item xs={12} sm={12}>
          <HolaText>
            Hola <strong>{values.name}</strong>,
          </HolaText>
          <UserValue>
            Thank you for registering for {values.event.charAt(0).toUpperCase() + values.event.slice(1)}.<br />
            Your payment details mail has been sent to {values.email}. <br />
            <br />
            <strong style={{ marginRight: '2vw' }}>Transaction ID</strong>
            {values.payment_id}
            
          </UserValue>

          <Divider variant="middle" style={{ margin: '4vh 0'}} />
          
        </Grid>
        
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    );
  }
}

export default EventSuccess;