import React, { Component } from 'react';
import { Typography, Grid, Select, MenuItem, InputLabel, TextField, Divider } from '@material-ui/core';
import TeamEvent from './TeamEvent';
import './Form.css';
import eventConfig from './eventConfig.json';

class EventForm extends Component{
  constructor(props){
    super(props);
    this.state={
      values:{
        module: '',
        moduleIndex: -1,
        event: ''
      }
    };
  }

  handleModuleSelection(key, value){
    this.setState((prevState)=>({
      ...prevState,
      values:{
        ...prevState.values,
        [key]: value
      }
    }));
  }

  onFormChange(value, field){
    this.setState((prevState)=>({
      ...prevState,
      values:{
        ...prevState.values,
        [field]: value
      }
    }))
  }

  render(){
    const {values} = this.state;
    let selectedEvent=[];
    if(values.module!==''){
      let finalEvent=eventConfig.filter((event)=>{
        return event.name===values.module;
      });
      selectedEvent=[
        ...finalEvent[0].event
      ];
    }
    return(
      <Grid container spacing={0}>
        <Grid style={{ textAlign: 'center' }} item sm={12}>
          <Typography component="h3"  variant="h3" style={{ fontFamily: 'Guardian', color: '#00d2ff' }}>
              Tecnoesis 2020
          </Typography>
          <Typography component="h1" variant="h4">
              Registration for Event
          </Typography>
          <Grid container spacing={0} style={{ margin: '20px 0' }}>
            <Grid item xs={6}>
              <InputLabel id="demo-simple-select-label">Module</InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={values.module}
                onChange={(event)=>{
                  this.handleModuleSelection('module', event.target.value)
                }}
              >
                <MenuItem value="" disabled>
                  Select Module
                </MenuItem>
                {eventConfig.map((event)=>{
                  return(
                    <MenuItem key={event.name} value={event.name}>{event.name.toUpperCase()}</MenuItem>
                  );
                })}
                
              </Select>  
            </Grid>
            <Grid item xs={6}>
              <InputLabel id="demo-simple-select-label">Events</InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={values.event}
                onChange={(event)=>{
                  this.handleModuleSelection('event', event.target.value)
                }}
              >
                <MenuItem value="" disabled>
                  Select Event
                </MenuItem>
                {selectedEvent && selectedEvent.map((event, index)=>{
                  return(
                    <MenuItem key={event.name} value={index}>{event.name.toUpperCase()}</MenuItem>
                  );
                })}                
              </Select>
             </Grid>
          </Grid>
          </Grid>
          
        {selectedEvent[values.event] && selectedEvent[values.event].team && 
          <TeamEvent event={selectedEvent[values.event]} moduleName={values.module} />}
      </Grid>
    );
  }
}

export default EventForm;