import React, { Component } from 'react';
import { Typography, Grid, Fab, TextField, Divider, Button, CircularProgress, FormControl, Tooltip, FormControlLabel, Radio, RadioGroup, Checkbox, Snackbar } from '@material-ui/core';
import axios from 'axios';
import  AddIcon from '@material-ui/icons/Add';
import  Delete from '@material-ui/icons/Delete';


class TeamEvent extends Component{
  constructor(props){
    super(props);
    this.state={
      submitSuccess: false,
      selectedEvent: props.event,
      prefillAddress: false,
      member:[{
        name: '',
        id: Date.now()
      }],
      formError:{
        email: ''
      },
      values:{
        member:[''],
        email: '',
        mobile: '',
        teamName: '',
        institute: '',
        city: '',
        zip: '',
        fee: "0"
      }
    };
  }

  handleSubmission(){

    this.setState({
      submitLoading: true
    });
    axios({
      method: 'POST',
      url: '/payment/event/pay',
      data: {
        ...this.state.values,
        module: this.props.moduleName,
        event: this.props.event.name
      }
    }).then((response)=>{
      console.log(response);
      this.setState({
        submitInfo:{
          ...response.data
        },
        submitLoading: false,
        submitSuccess: true
      }, ()=>{
        const {submitInfo, values}= this.state;
        const script = document.createElement("script");
        script.onload=function(){
          document.getElementsByClassName('razorpay-payment-button')[0].style.display='none';
          document.getElementsByClassName('razorpay-payment-button')[0].click();
        }
        const attributes={
          'data-key': submitInfo.key,
          'data-amount': submitInfo.amount ,
          'data-currency':"INR",
          'data-order_id': submitInfo.orderID,
          'data-buttontext': "Continue to Payment",
          'data-name': "Tecnoesis 2020",
          'data-description': "Tecnoesis 2020 NIT Silchar",
          'data-prefill.name' :values.name,
          'data-prefill.email': values.email,
          'data-prefill.contact': values.mobile
        };
        script.src = "https://checkout.razorpay.com/v1/checkout.js";

        for(var key in attributes){
          script.setAttribute(key, attributes[key]);
        }
        script.async = true;
        document.forms['reg'].appendChild(script);
      });
    }).catch((e)=>{
      console.log(e.response);
      if(e.response.data.error==='email'){
        this.setState({
          submitLoading: false,
          error: true,
          formError:{
            email: 'Email Already Registered'
          },
          errorMessage: 'Email Already Registred'
        });
      }
      else{
        this.setState({
          submitLoading: false,
          error: true,
          errorMessage: 'Error Occurred'
        });
      }
    });
  }

  onFormChange(value, field, index){
    if(field==='member'){
      this.setState((prevState)=>{
        const newMember=[...prevState.member];
        newMember[index].name=value;

        const valueMember=[...prevState.values.member];
        valueMember[index]=value;
        return({
          ...prevState,
          member: [...newMember],
          values:{
            ...prevState.values,
            member: [...valueMember]
          }
        })
      })
    }
    else{
      this.setState((prevState)=>({
        ...prevState,
        values:{
          ...prevState.values,
          [field]: value.toString()
        }
      }))
    }
  }
  
  addTeamMember(){
    if(this.state.values.member.length<=3){
      this.setState((prevState)=>{
        const newMember= [...prevState.member];
        newMember.push({
          name: '',
          id: Date.now()
        });

        const valueMember=[...prevState.values.member];
        valueMember.push('');
        return({
          ...prevState,
          member: [...newMember],
          values:{
            ...prevState.values,
            member: [...valueMember]
          }
        });
      });
    }
  }
  deleteTeamMember(index){
    if(this.state.values.member.length!==1){
      this.setState((prevState)=>{
        const newMember= [...prevState.member];
        newMember.splice(index,1);

        const valueMember=[...prevState.values.member];
        valueMember.splice(index,1);
        return({
          ...prevState,
          member: [...newMember],
          values:{
            ...prevState.values,
            member: [...valueMember]
          }
        });
      });
    }
  }

  render(){
    console.log('FEE', this.state.values);
    const {values, selectedEvent, member, submitSuccess, formError} = this.state;
    let isDisabled = (values.email===''||values.mobile===''||values.teamName===''||values.institute===''||values.city===''||values.zip==='');
    // values.member.forEach(element => {
    //   isDisabled=isDisabled||element==='';
    // });
    return(
      <React.Fragment>
        <Grid container spacing={0}>
          <Grid item sm={12} md={6} style={{ padding: '2vh 5vw', borderRight: '1px solid #ebebeb' }}>
              <Typography variant="h6" component="h6">
                Information
              </Typography>
              <TextField
                required
                name="teamName"
                label="Team Name"
                value={values.teamName || ''}
                fullWidth
                onChange={(event)=>{
                  this.onFormChange(event.target.value, 'teamName');
                }}
                style={{ margin: '2vh 0vw'}}
              />
              <TextField
                required
                name="email"
                label="Email"
                value={values.email || ''}
                error={formError.email!==''}
                helperText={formError.email}
                fullWidth
                onChange={(event)=>{
                  this.onFormChange(event.target.value, 'email');
                }}
                style={{ margin: '2vh 0vw'}}
              />
              <TextField
                required
                name="mobile"
                label="Mobile"
                value={values.mobile || ''}
                fullWidth
                onChange={(event)=>{
                  this.onFormChange(event.target.value, 'mobile');
                }}
                style={{ margin: '2vh 0vw'}}
              />
          </Grid>
          {/* <Divider orientation="vertical" /> */}
          <Grid item sm={12} md={6} style={{ padding: '2vh 2vw' }}>
            <Typography variant="h6" component="h6" style={{ display: 'inline-block', marginRight: '2vw'}}>
              Team Info
            </Typography>
            <Fab variant="extended" aria-label="add" onClick={()=>this.addTeamMember()}>
              <AddIcon />
              Add Member
            </Fab>
              {member && member.map((person, index)=>{
                return(
                <div style={{ position: 'relative' }} key={person}>
                  <TextField
                    required
                    name="member"
                    id={person.id}
                    label={`Member ${index+1}`}
                    value={person.name || ''}
                    onChange={(event)=>{
                      this.onFormChange(event.target.value, 'member', index);
                    }}
                    style={{ margin: '2vh 0vw'}}
                  />
                    <Delete
                      style={{ position: 'absolute', right: '0', top: '30px', cursor: 'pointer' }}
                      onClick={()=> this.deleteTeamMember(index)}
                    />
                </div>);
              })}
          </Grid>
        </Grid>
        <Grid container style={{ padding: '2vh 5vw' }}>
            <Typography variant="h6" component="h6">
              College Information
            </Typography>
            <Grid item sm={12}>
              <Checkbox
                checked={this.state.prefillAddress}
                onChange={()=>{
                  this.setState((prevState)=>({
                    prefillAddress: !prevState.prefillAddress,
                    values:{
                      ...prevState.values,
                      institute: 'NIT Silchar',
                      city: 'Silchar',
                      zip: '788010'
                    }
                  }))
                }}
                value="primary"
                inputProps={{ 'aria-label': 'primary checkbox' }}
              />
              <span>
                From NIT Silchar
              </span>
            </Grid>
            <TextField
              required
              name="institute"
              label="Institute Name"
              value={values.institute || ''}
              fullWidth
              disabled={this.state.prefillAddress}
              onChange={(event)=>{
                this.onFormChange(event.target.value, 'institute');
              }}
              style={{ margin: '2vh 0vw'}}
            />
            <Grid container spacing={4}>
              <Grid item sm={12} md={6}>
                <TextField
                  required
                  name="city"
                  label="City"
                  disabled={this.state.prefillAddress}
                  value={values.city || ''}
                  fullWidth
                  onChange={(event)=>{
                    this.onFormChange(event.target.value, 'city');
                  }}
                  style={{ margin: '2vh 0vw'}}
                />
              </Grid>
              <Grid item sm={12} md={6}>
                <TextField
                required
                name="zip"
                disabled={this.state.prefillAddress}
                label="Zip Code"
                value={values.zip || ''}
                onChange={(event)=>{
                  this.onFormChange(event.target.value, 'zip');
                }}
                style={{ margin: '2vh 0vw'}}
              />
              </Grid>
            </Grid>
        </Grid>
        <Divider />
        <Grid container style={{ padding: '2vh 5vw' }}>
        <Grid item xs={12} md={12} style={{ textAlign: 'center', margin: '4vh 0' }}>
          <Grid item xs={12} md={12}>
          <FormControl component="fieldset">
            <RadioGroup aria-label="gender" name="gender1" value={values.fee.toString()} onChange={(event)=>{
              this.onFormChange(event.target.value, 'fee');
            }}>
            {this.props.event && this.props.event.fee.length>1 && this.props.event.fee.map((fee)=>{
                return(
                  <Tooltip title={fee.desc} placement="right-start" key={fee.label} >
                    <FormControlLabel value={fee.amount.toString()} control={<Radio />} label={fee.label} />
                  </Tooltip>
                );
              })}
            </RadioGroup>
          </FormControl>
          
          {/* <FormControl component="fieldset">
            <RadioGroup aria-label="fee" name="fee" value={values.fee} onChange={(e)=>{
              this.onFormChange(e.target.value, 'fee')
            }}>
              <FormControlLabel value={"244"} control={<Radio />} label={"FF"}  />
              {/* {this.props.event && this.props.event.fee.length>1 && this.props.event.fee.map((fee)=>{
                return(
                  <Tooltip title={fee.desc} placement="right-start" key={fee.label} id={fee.amount} >
                  </Tooltip>
                );
              })} */}
            {/* </RadioGroup>
        </FormControl> */}
          </Grid>
          <TextField
            required
            name="fee"
            label="Fee"
            disabled
            value={values.fee}
            style={{ margin: '2vh 0vw'}}
          />
          <br />
          <Button
            variant="contained"
            disabled={isDisabled || this.state.submitLoading || this.state.submitSuccess}
            style={{ backgroundColor: '#007792', color: '#fff', marginRight: '20px' }}
            onClick={()=>{
              this.handleSubmission();
            }}
          >
            {this.state.submitLoading && (<CircularProgress style={{ color: "#fff" }} />)}
            {!this.state.submitLoading && ("Continue To Payment")}
          </Button>

          {submitSuccess && (
            <form action="/payment/event/success/" method="POST" name="reg">
              <input type="hidden" custom="Hidden Element" name="hidden" />
            </form>
          )}
          </Grid>
        </Grid>
        <Snackbar
          open={this.state.error}
          anchorOrigin={{ horizontal: 'right', vertical: 'top' }}
          message={this.state.errorMessage}
          variant="error"
          onClose={()=> {
            this.setState({
              error: false
            })
          }}
          autoHideDuration={5000}
        />
        
     
     </React.Fragment>
    );
  }
}

export default TeamEvent;